html,
body {
  padding: 0;
  margin: 0;
  font-family: "Rubik", sans-serif;
  direction: rtl;
  background-color: #eee9da;
}

.title-wrapper {
  padding: 0 32px;
  height: 50vh;
}

.title-image-wrapper {
  width: 40vw;
}

.title {
  font-size: 8vw;
  font-weight: 400;
  color: #614923;
  line-height: 6vw;
}

.title .sub {
  font-size: 4vw;
  font-weight: 400;
  color: #93784c;
}

.about-wrapper {
  background-color: #6096b4;
  padding: 32px;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 32px;
}

.about-wrapper a {
  background-color: #eee9da;
  border: none;
  border-radius: 4px;
  color: #6096b4;
  font-size: 22px;
  padding: 8px;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about {
  color: white;
  font-size: 22px;
  text-align: center;
}

.flex {
  display: flex;
}

.flex.gap-16 {
  gap: 16px;
}

.flex.center {
  align-items: center;
}

.flex.sb {
  justify-content: space-between;
}

@media only screen and (max-width: 600px) {
  .col-on-resize {
    flex-direction: column;
    justify-content: space-around;
  }

  .flex.sb {
    justify-content: space-around;
  }

  .title {
    font-size: 15vw;
    line-height: 10vw;
  }

  .title .sub {
    font-size: 8vw;
  }

  .title-wrapper {
    text-align: center;
  }

  .title-image-wrapper {
    width: 60vw;
  }
}
